.toggleButton{
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggleButton:focus{
    outline: none;
}

.toggleButtonLine{
    width: 30px;
    height: 2px;
    background: white;
}